html, body {
  height: 100%;
}

.wrapper {
  font-size: 1.6rem;
  line-height: 2.1rem;
  color: #000000;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.main {
  min-height: 100%;
  flex: 1 1 auto;
}

[class*="__container"] {
  max-width: 105.4rem;
  margin: 0 auto;
  padding: 0 1.5rem;
}
